#slider {
  position: relative;
  background-color: $secondaryColor;

  & .container-full {
    width: 100%;
    padding-top: 320px;
    padding-bottom: 120px;

    background-color: $secondaryColor;
  }

  & h3.title-h2 {
    max-width: 740px;
  }
}

.slider__contacts {
  margin-top: 60px;
}

.slider__contacts-left {
  display: flex;
}

.slider__contacts-left > * + * {
  margin-left: 112px;
}

.slider__contacts-links > * + * {
  margin-top: 24px;
}

.links-item {
  display: flex;
  padding-left: 170px;
  position: relative;
  align-items: center;

  & span {
    position: absolute;
    left: 0;

    font-weight: 600;
    font-size: 32px;
    line-height: 1.3;
    color: #9ccaff;
  }

  & a {
    color: $primaryColor;
  }
}

.links-item > * + * {
  margin-left: 56px;
}

.slider__contacts-socials > * + * {
  margin-top: 26px;
}

.socials-item {
  display: flex;
  align-items: center;

  color: $primaryColor;
}

.socials-item > * + * {
  margin-left: 16px;
}

.slider__contacts-right {
  margin-top: 95px;
}

.swiper-wrapper {
  align-items: center;
}
